import { NgModule, ErrorHandler }		                                      from '@angular/core';
import { HttpModule }                                                         from "@angular/http";
import { BrowserModule }                                                      from '@angular/platform-browser';
import { FormsModule, ReactiveFormsModule }   								  from '@angular/forms';

import { IonicApp, IonicModule, IonicErrorHandler, DeepLinkConfig }           from 'ionic-angular';
import { StatusBar }                                                          from '@ionic-native/status-bar';
import { SuperTabsModule }                                                    from 'ionic2-super-tabs';
import { AgmCoreModule }                                                      from '@agm/core';
import { ChartsModule }                                                       from 'ng2-charts';


import { C8o }                                                                from "c8osdkangular";
import { C8oRouter } 			                                              from 'c8ocaf';

import { MyApp } 				                                              from './app.component';
import {Transfer, TransferObject} from '@ionic-native/transfer';
import {File} from '@ionic-native/file';
import { FileOpener } from '@ionic-native/file-opener';
import { StreamingMedia } from '@ionic-native/streaming-media';
import { SocialSharing } from '@ionic-native/social-sharing';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { TabsPage } from "../pages/TabsPage/tabspage";
import { NewsPage } from "../pages/NewsPage/newspage";
import { ProgressPage } from "../pages/ProgressPage/progresspage";
import { DetailsNewsPage } from "../pages/DetailsNewsPage/detailsnewspage";
import { DocumentsPage } from "../pages/DocumentsPage/documentspage";
import { AstucesPage } from "../pages/AstucesPage/astucespage";
import { SondagesPage } from "../pages/SondagesPage/sondagespage";
import { SettingsPage } from "../pages/SettingsPage/settingspage";
import { PlusPage } from "../pages/PlusPage/pluspage";



/**
 * Deep links to your pages so that the app can rout directly to the page url
 */
export const deepLinkConfig: DeepLinkConfig = {
  links: [ { component: TabsPage, name: "TabsPage", segment: "TabsPage" }, { component: NewsPage, name: "NewsPage", segment: "Page" }, { component: ProgressPage, name: "ProgressPage", segment: "ProgressPage" }, { component: DetailsNewsPage, name: "DetailsNewsPage", segment: "DetailsNewsPage" }, { component: DocumentsPage, name: "DocumentsPage", segment: "DocumentsPage" }, { component: AstucesPage, name: "AstucesPage", segment: "AstucesPage" }, { component: SondagesPage, name: "SondagesPage", segment: "AstucesPage1" }, { component: SettingsPage, name: "SettingsPage", segment: "SettingsPage" }, { component: PlusPage, name: "PlusPage", segment: "PlusPage" }]
};

@NgModule({
  declarations: [
    MyApp, TabsPage, NewsPage, ProgressPage, DetailsNewsPage, DocumentsPage, AstucesPage, SondagesPage, SettingsPage, PlusPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
	FormsModule,
	ReactiveFormsModule,
	ChartsModule,
	SuperTabsModule.forRoot(),
	AgmCoreModule.forRoot({
	      apiKey: 'AIzaSyB0Nl1dX0kEsB5QZaNf6m-tnb1N-U5dpXs'
	}),
    IonicModule.forRoot(MyApp, {}, deepLinkConfig)
  ],
  bootstrap: [IonicApp],
  entryComponents: [MyApp, TabsPage, NewsPage, ProgressPage, DetailsNewsPage, DocumentsPage, AstucesPage, SondagesPage, SettingsPage, PlusPage],
  providers: [
    StatusBar,
    C8o,
    C8oRouter,
      Transfer,
      TransferObject,
      File,
      FileOpener,
      StreamingMedia,
      SocialSharing,
      InAppBrowser,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})

export class AppModule {}


