import { Component }						from '@angular/core';
import { DomSanitizer }                 	from '@angular/platform-browser';
import { NavParams, LoadingController, MenuController }   	from 'ionic-angular';
import { C8oRouter }                    	from 'c8ocaf';
import { C8oPage }                      	from 'c8ocaf';
import {ChangeDetectorRef, ChangeDetectionStrategy, InjectionToken, Injector, Type} from "@angular/core";

/*
	You can customize your page class by writing code between the :

   		Begin_c8o_XXXX and
   		End_c8o_XXXX
   		
   	Comments.
   	
   	Any code placed outside these these comments will be lost when the application is generated
*/

     
/*Begin_c8o_PageImport*/
import { List, App, FabContainer, ModalController, AlertController } from 'ionic-angular';
import { SocialSharing } from '@ionic-native/social-sharing';
/*End_c8o_PageImport*/

@Component({
  selector: 'page-detailsnewspage',
  templateUrl: 'detailsnewspage.html',
})
export class DetailsNewsPage extends C8oPage  {
	
	
	/*Begin_c8o_PageDeclaration*/
    socialSharing: SocialSharing;
	/*End_c8o_PageDeclaration*/
	
	constructor(routerProvider : C8oRouter, navParams: NavParams, loadingCtrl: LoadingController, sanitizer: DomSanitizer, ref: ChangeDetectorRef, injector: Injector, menuCtrl: MenuController){
		super(routerProvider, navParams, loadingCtrl, sanitizer, ref, injector, menuCtrl);
		
		
		
		/*Begin_c8o_PageConstructor*/
		this.socialSharing = this.getInstance(SocialSharing);
		/*End_c8o_PageConstructor*/
		
    }
	
	/*Begin_c8o_PageFunction*/
	public openSocial(network:string, fab: FabContainer){
	    let loading = this.loadingCtrl.create({
            content: `${network} indisponible en mode navigateur`,
            duration: 3000
          });
          loading.onWillDismiss(() => {
            fab.close();
          });
          loading.present();    
    }
	/*End_c8o_PageFunction*/
	
	
	/**
	 * Function openTwitter
	 *   
	 * 
	 * @param event , the event received
	 * @param label , 
	 * @param container , 
	 */
	CTS1505944628324(event, label, container) {
	/*Begin_c8o_function:CTS1505944628324*/
	    
	    
	    if(window["cordova"] == undefined){
	        window["cblite"].getURL(function (err, url) {
	            if (err) {
	                console.log("error launching Couchbase Lite: " + err)
	            } else {
	                console.log("Couchbase Lite running at " + url);
	            }
	        });
            this.openSocial(label, container);
        }else{
             let loading = this.loadingCtrl.create({
                
              });
                loading.present();   
                this.socialSharing.shareViaTwitter(this.listen(['fs://monmob.get#getDetailsNews']).description, null, null).then(() => {
                    console.log("shared");
                    container.close();
                    loading.dismiss();
                }).catch(() => {
                  // Error!
                    container.close();
                    loading.dismiss();
                    this.openSocial(label, container);
                    console.log('error sharing');
                });
        }
	/*End_c8o_function:CTS1505944628324*/
	}

	/**
	 * Function openFacebook
	 *   
	 * 
	 * @param event , the event received
	 * @param label , 
	 * @param container , 
	 */
	CTS1505944628411(event, label, container) {
	/*Begin_c8o_function:CTS1505944628411*/
	    if(window["cordova"] == undefined){
            this.openSocial(label, container);
        }else{
            let loading = this.loadingCtrl.create({
                
            });
              loading.present();
                this.socialSharing.shareViaFacebook(this.listen(['fs://monmob.get#getDetailsNews']).description, null, null).then(() => {
                    console.log("shared");
                    container.close();
                    loading.dismiss();
                }).catch(() => {
                  // Error!
                    container.close();
                    loading.dismiss();
                    this.openSocial(label, container);
                    console.log('error sharing');
                });
        }
                
	    this.tick();
	/*End_c8o_function:CTS1505944628411*/
	}

	/**
	 * Function openEmail
	 *   
	 * 
	 * @param event , the event received
	 * @param label , 
	 * @param container , 
	 */
	CTS1505945771559(event, label, container) {
	/*Begin_c8o_function:CTS1505945771559*/
	    if(window["cordova"] == undefined){
	        this.openSocial(label, container);
	    }else{
	        let loading = this.loadingCtrl.create({
                
            });
              loading.present(); 
	        this.socialSharing.canShareViaEmail().then(() => {
	            // Sharing via email is possible
	            this.socialSharing.shareViaEmail(this.listen(['fs://monmob.get#getDetailsNews']).description, this.listen(['fs://monmob.get#getDetailsNews']).titre, ['']).then(() => {
	                // Success!
	                console.log('succes');
	                container.close();
                    loading.dismiss();
	              }).catch(() => {
	                // Error!
	                  container.close();
	                    loading.dismiss();
	                  console.log('error sharing');
	              });
	          }).catch(() => {
	              console.log('sharing not possible');
	              
	              container.close();
                  loading.dismiss();
	            // Sharing via email is not possible
	          });
	    }
	    this.tick();
	/*End_c8o_function:CTS1505945771559*/
	}

}



